import { statStyle } from "../core/model/Stat";
import catImg from "../../assets/images/cat.jpg";
// interface ParagraphPlus extends Paragraphs {
//   special?: string;
// }
/**
 * Entry point. Contains all game content.
 * @param player
 * @returns paragraphs
 */
export function getParagraph(player) {
    let paragraphs = [
        {
            name: "start",
            content: `You start the game.`,
            choices: [
                {
                    id: 1,
                    content: `Kill the dragon?`,
                    nextName: `kill-dragon`
                },
                {
                    id: 2,
                    content: `Let the dragon `,
                    nextName: `let-burn`
                },
            ],
        },
        {
            name: "kill-dragon",
            content: `Villagers are very happy
        <img src="${catImg}">`,
            choices: [
                {
                    id: 1,
                    content: `But there's more`,
                    nextName: `dragon-revenge`
                },
            ],
        },
        {
            name: "dragon-revenge",
            content: `The dragons are back to revenge!`,
            choices: [
                {
                    id: 1,
                    content: `Are you prepared enough?`,
                    nextName: `battle`,
                    consequence: {
                        stat: [
                            { statName: `skilled`, value: 10, style: statStyle.show },
                        ],
                    }
                },
                {
                    id: 2,
                    content: `not prepared! Panic!`,
                    nextName: `battle`,
                    consequence: {
                        stat: [
                            { statName: `skilled`, value: 0, style: statStyle.show },
                        ]
                    }
                },
            ],
        },
        {
            name: "battle",
            content: `Kill the dragons!`,
            choices: [
                {
                    id: 1,
                    content: `Kill it!`,
                    nextName: `end-1`,
                    precondition: {
                        stat: [
                            { statName: `skilled`, value: 10, },
                        ],
                    },
                },
                {
                    id: 2,
                    content: `Run`,
                    nextName: `end-2`
                },
            ],
        },
        {
            name: "end-1",
            content: `Everyone lives! hooray`,
        },
        {
            name: "end-2",
            content: `Everyone dies. Oh no.`,
        },
        {
            name: "let-burn",
            content: `The village burned to the ground. But you got coin from the dragon.`,
            choices: [
                {
                    id: 1,
                    content: `Go about your way`,
                    nextName: `end-3`
                },
            ],
        },
        {
            name: "end-3",
            content: `You are hunted across the country.
  <br><b>The End</b>`,
        },
    ];
    return paragraphs;
}
